#myFeed-Div{
    /* height: 100vh;
    overflow: auto hidden; */
    margin-top: 50px;
    position: relative;
}
#textArea{
    width: 27vw;
    height: 70px;
    border: 1px solid black;
}
#sharePostBtn{
    width: 9vw;
    height: 30px;
    position: absolute;
    margin-left: 10px;
    margin-top: 45px;
    background-color: rgb(50,100,255);
    color: white;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    font-size: 18px;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}


@media(max-width:768px) {
    #textArea{
        width: 60vw;
        margin-left: -40px;
    }
    #sharePostBtn{
        width: 18vw;
    }
}